<template>
	<!-- 商品（没有点批量报价的商品） -->
	<view class="commodity">
		<!-- 输入框 -->
		<view class="inp_area">
			<!-- 全选 -->
			<view class="allChoose">
				<u-checkbox v-model="allChecked" :active-color='activeColor' @change="checkboxAllChange">
					<text class="allChoose_text">全选</text>
				</u-checkbox>
			</view>
			<u-field v-model="name" placeholder="请输入产品编号或名称" label-width="0" class="ufield" :border-bottom="false">
			</u-field>
			<u-button @click="checkedAll" type="error" size='mini' class="search_btn">搜索</u-button>
		</view>
		<view class="commodity_list_tips" @click="toSoonExpire">您有{{day3AfterList.length}}个产品价格将在3天之后过期</view>

		<!-- 商品列表 -->
		<view class="commodity_list">
			<u-checkbox-group :wrap="true" :active-color='activeColor'>
				<u-checkbox @change="checkboxOneChange" v-model="item.checked" v-for="(item, index) in list" :key="item.id"
				 :name="item.name">
					<view class="checkbox_view">
						<view class="checkbox_view_oneline">
							<text :class="{checkbox_view_name:true,gray:item.isGray}">{{item.spuName}}</text>
							<text class="mg15">:</text>
							<text :class="{gray:item.isGray,deepgray:!item.isGray}">{{item.brand}}</text>
						</view>

						<view class="checkbox_view_oneline">
							<text :class="{gray:item.isGray,deepgray:!item.isGray}">规格</text>
							<text class="mg15">:</text>
							<text :class="{gray:item.isGray,deepgray:!item.isGray}">{{item.spuSpec}}</text>
						</view>

						<view class="checkbox_view_oneline">
							<text :class="{gray:item.isGray,deepgray:!item.isGray}">最新报价(USD)</text>
							<text class="mg15">:</text>
							<text :class="{gray:item.isGray,deepgray:!item.isGray}">{{item.bidAmount?item.bidAmount:"未报价"}}</text>
							<u-tag text="已失效" type="info" class="utag" v-if="item.isGray"/>
							<u-tag text="三天后到期" type="error" plain class="utag" v-if="item.day3After"/>
						</view>
						<view class="checkbox_view_oneline">
							<text :class="{gray:item.isGray,deepgray:!item.isGray}">有效期</text>
							<text class="mg15">:</text>
							<text :class="{gray:item.isGray,deepgray:!item.isGray}">{{item.expiredDate||'NA'}}</text>
						</view>
						<view class="checkbox_view_oneline">
							<u-button type="error" size="mini" class="checkbox_view_oneline_btn" plain>更新报价</u-button>
						</view>
					</view>
				</u-checkbox>
			</u-checkbox-group>
		</view>
		<view class="btn">
			<u-button @click="batchFilling" type="error" plain>批量填写</u-button>
		</view>
		<!-- 底部导航 -->
		<Tabbar></Tabbar>
	</view>
</template>

<script>
	import Tabbar from '../../my_common_components/Tabbar.vue'
	import fetch from '../../utils/fetch.js'
	export default {
		components: {
			Tabbar
		},
        
		data() {
			return {
				name: "",
				//默认checkbox选中颜色
				activeColor: "#D0021B",
				//全选
				allChecked: false,
				//三天后过期的时间戳
				day3After:(new Date(new Date(new Date().toLocaleDateString()).getTime()+3*24*60*60*1000-1)).valueOf(),
			   //所有数据
				list: [ {
            bidAmount: 200,
            cur: "USD",
            enSpuName: "",
            expiredDate: "2020-09-08",
            skuCode: "",
            skuId: 1186884,
            spuId: 6359071,
            spuName: "冯振鑫商品",
            spuSpec: "SW001",
            state: 0,
            unit: 18
          },
          {
            bidAmount: 200,
            cur: "USD",
            enSpuName: "",
            expiredDate: "2020-09-11",
            skuCode: "",
            skuId: 1186884,
            spuId: 6359071,
            spuName: "冯振鑫商品",
            spuSpec: "SW001",
            state: 0,
            unit: 18
          },
          {
            bidAmount: 200,
            cur: "USD",
            enSpuName: "",
            expiredDate: "2020-09-12",
            skuCode: "",
            skuId: 1186884,
            spuId: 6359071,
            spuName: "冯振鑫商品",
            spuSpec: "SW001",
            state: 0,
            unit: 18
          },
          {
            bidAmount: 200,
            cur: "USD",
            enSpuName: "",
            expiredDate: "2020-09-15",
            skuCode: "",
            skuId: 1186884,
            spuId: 6359071,
            spuName: "冯振鑫商品",
            spuSpec: "SW001",
            state: 0,
            unit: 18
          }],
			};
		},

		created() {
			this.getSupplierProduct()
		},
		methods: {
			//获取商品列表
			async getSupplierProduct() {
				let res = await fetch(this.api.v2.supplier_product, {
					method: "get",
					data: {
						accessToken:uni.getStorageSync('accessToken'),
						keyword:this.name
					}
				})
			
				if (res.data.code === '0') {
					this.list=res.data.data
					this.list.push(
					 {
					            bidAmount: 200,
					            cur: "USD",
					            enSpuName: "",
					            expiredDate: "2020-09-08",
					            skuCode: "",
					            skuId: 1186884,
					            spuId: 6359071,
					            spuName: "冯振鑫商品",
					            spuSpec: "SW001",
					            state: 0,
					            unit: 18
					          },
					          {
					            bidAmount: 200,
					            cur: "USD",
					            enSpuName: "",
					            expiredDate: "2020-09-11",
					            skuCode: "",
					            skuId: 1186884,
					            spuId: 6359071,
					            spuName: "冯振鑫商品",
					            spuSpec: "SW001",
					            state: 0,
					            unit: 18
					          },
					          {
					            bidAmount: 200,
					            cur: "USD",
					            enSpuName: "",
					            expiredDate: "2020-09-12",
					            skuCode: "",
					            skuId: 1186884,
					            spuId: 6359071,
					            spuName: "冯振鑫商品",
					            spuSpec: "SW001",
					            state: 0,
					            unit: 18
					          },
					          {
					            bidAmount: 200,
					            cur: "USD",
					            enSpuName: "",
					            expiredDate: "2020-09-15",
					            skuCode: "",
					            skuId: 1186884,
					            spuId: 6359071,
					            spuName: "冯振鑫商品",
					            spuSpec: "SW001",
					            state: 0,
					            unit: 18
					          })
					if (this.list.length > 0) {
						this.list.forEach((item, index) => {
							item.checked = false
							item.down = false
							item.name = index
							item.id = index
							item.isGray=new Date(item.expiredDate).getTime()<(+new Date())
							item.day3After=((new Date()).valueOf()<new Date(item.expiredDate).getTime())&&(new Date(item.expiredDate).getTime()<this.day3After)
						})
					}
				}
			},
			// 全选
			checkboxAllChange() {
				this.allChecked ?
					this.list.map((val) => {
						val.checked = true;
					}) :
					this.list.map((val) => {
						val.checked = false;
					});
				this.$forceUpdate()
			},

			//单选
			checkboxOneChange(e) {
				this.allChecked = (this.list.length === this.list.filter(val => val.checked).length)
				this.$forceUpdate()
			},

			//批量填写
			batchFilling() {
				uni.navigateTo({
					url: '../batchQuotation/index'
				});
			},
			
			//点击去过期页面
			toSoonExpire() {
				uni.setStorageSync('day3AfterList', this.day3AfterList)
				uni.navigateTo({
					url: '../soonExpire/index'
				});
			},
			
			//点击搜索
			checkedAll(){
				this.getSupplierProduct()
			}

		},
		computed:{
			//三天后就过期的数据
			day3AfterList(){
				return this.list.filter(item=>{
				 return	((new Date()).valueOf()<new Date(item.expiredDate).getTime())&&(new Date(item.expiredDate).getTime()<this.day3After)
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	//公共样式
	.gray {
		color: #c9c9c9 !important;
	}
	.deepgray{
		color:#868686 !important;
	}

	.commodity {
		.inp_area {
			padding: 0 30rpx 0 10rpx;
			display: flex;

			.allChoose {
				padding-left: 30rpx;
				align-self: center;
				padding-top: 2rpx;
			}

			.search_btn {
				// float: right;
				align-self: center;
			}

			.ufield {
				flex: 1
			}
		}

		.commodity_list_tips {
			background-color: #f2f2f2;
			height: 70rpx;
			color: #D0021B;
			text-align: center;
			line-height: 70rpx;
		}

		//商品列表
		.commodity_list {
			padding: 0 40rpx;

			/deep/ .u-checkbox {
				border-bottom: 2rpx solid #f6f6f6;
			}

			.checkbox_view {
				margin-left: 20rpx;
				padding: 25rpx 0;
				color: #868686;
				.checkbox_view_name {
					font-weight: 700;
					font-size: 32rpx;
					color: #666;
				}

				.utag {
					margin-left: 30rpx;
				}
				.checkbox_view_oneline{
					font-size: 24rpx;
				}
			}
		}

		.btn {
			padding: 0 40rpx;
		}
	}
</style>
